<template>
	<view>
		<view class="footer">
			<view :class="'footer_item ' + (item.size=='big'?'big_item':'')  " :style="'width:'+item_width" @click="change_nav(index)" v-for="(item,index) in footer_nav" :key="index">
				<image :src="index==now_index?item.select_icon:item.icon" mode=""></image>
				<view :class="{ active: index==now_index }">{{item.name}}</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				
			};
		},
		methods:{
			change_nav(index){
				// let access_token = uni.getStorageSync('access_token');
				// if(!access_token){
				// 	uni.showModal({
				// 		title: '您尚未登录',
				// 		content: '是否前往登录页面',
				// 		confirmText: '前往',
				// 		success: (res) => {
				// 			if (res.confirm) {
				// 				uni.navigateTo({
				// 					url: '../login/login',
				// 				})
				// 				return false;
				// 			} else if (res.cancel) {
				// 				util.errorTips('您点击了取消');
				// 			}
				// 		}
				// 	})
				// 	return false
				// }
				let myStatus = uni.getStorageSync('myStatus');
				if(myStatus == 1){
					if(index == 0){
						uni.setNavigationBarTitle({
							title: "一鹿找"
						})
						uni.setNavigationBarColor({
							frontColor: '#000000',
							backgroundColor: '#FFFFFF',
							animation: {
								duration: 400,
								timingFunc: 'easeIn'
							}
						})
					}else if(index == 1){
						uni.setNavigationBarTitle({
							title: "任务中心"
						})
						uni.setNavigationBarColor({
							frontColor: '#000000',
							backgroundColor: '#FFFFFF',
							animation: {
								duration: 400,
								timingFunc: 'easeIn'
							}
						})
					}else if(index == 2){
						uni.setNavigationBarTitle({
							title: "订单列表"
						})
						uni.setNavigationBarColor({
							frontColor: '#000000',
							backgroundColor: '#FFFFFF',
							animation: {
								duration: 400,
								timingFunc: 'easeIn'
							}
						})
					}else if(index == 3){
						uni.setNavigationBarTitle({
							title: "个人中心"
						})
						uni.setNavigationBarColor({
							frontColor: '#ffffff',
							backgroundColor: '#F29800',
							animation: {
								duration: 400,
								timingFunc: 'easeIn'
							}
						})
					}
				}else{
					if(index == 0){
						uni.setNavigationBarTitle({
							title: "订单"
						})
						uni.setNavigationBarColor({
							frontColor: '#000000',
							backgroundColor: '#FFFFFF',
							animation: {
								duration: 400,
								timingFunc: 'easeIn'
							}
						})
					}else if(index == 1){
						uni.setNavigationBarTitle({
							title: "消息"
						})
						uni.setNavigationBarColor({
							frontColor: '#000000',
							backgroundColor: '#FFFFFF',
							animation: {
								duration: 400,
								timingFunc: 'easeIn'
							}
						})
					}else if(index == 2){
						uni.setNavigationBarTitle({
							title: "个人中心"
						})
						uni.setNavigationBarColor({
							frontColor: '#ffffff',
							backgroundColor: '#F29800',
							animation: {
								duration: 400,
								timingFunc: 'easeIn'
							}
						})
						
						
					}
				}
				
				this.$store.commit("change_page",index)
			}
		},
		computed:{
			item_width(){
				let length = this.$store.state.footer_store.footer_nav.length;
				switch (length){
					case 1:
						return '100%'
						break;
					case 2:
						return '50%'
						break;
					case 3:
						return '33.3%'
						break;
					case 4:
						return '25%'
						break;
					case 5:
						return '20%'
						break;
					default:
						break;
				}
			},
			footer_nav(){
				console.log(this.$store.state.footer_store.footer_nav)
				return this.$store.state.footer_store.footer_nav
			},
			now_index(){
				return this.$store.state.footer_store.now_page_index;
			}
		}
	}
</script>

<style>
.footer{
	height: 120upx;
	position: fixed;
	bottom: 0;
	left: 0;
	width: 100%;
	background-color: #fff;
	color: #333;
	border-top: 1upx solid #eee;
	padding-top: 10upx;;
	z-index: 99999999;
}
.footer_item{
	float: left;
	width: 33.3%;
	text-align: center;
	font-size: 28upx;
	height: 120upx;
 
}
.footer_item image{
	width: 60upx;
	height: 60upx;
}
.footer_item.big_item{
	position: relative;
	top: -40upx;
}
.footer_item.big_item image{
	width: 100upx;
	height: 100upx;
}
.active{
	color: orange;
}
</style>
